// @ts-ignore;
import React, { useState } from 'react';
// @ts-ignore;
import { useToast } from '@/components/ui';

export default function GanttChart() {
  const {
    toast
  } = useToast();
  const [viewMode, setViewMode] = useState('week');

  // 
  const tasks = [{
    server: ' A',
  tasks: [{    name: 'BLAST',
    start: 10,
    duration: 30,
    color: 'bg-blue-500'
  }, {
    name: '',
    start: 45,
    duration: 25,
    color: 'bg-green-500'
  }, {
    name: '',
    start: 75,
    duration: 20,
    color: 'bg-amber-500'
  }]
  }, {
    server: ' B',
  tasks: [{    name: '',
    start: 20,
    duration: 40,
    color: 'bg-purple-500'
  }, {
    name: '',
    start: 65,
    duration: 30,
    color: 'bg-amber-500'
  }]
  }, {
    server: ' C',
    tasks: [{
      name: '',
      start: 15,
      duration: 35,
      color: 'bg-amber-500'
    }, {
      name: '',
      start: 60,
      duration: 15,
      color: 'bg-red-500'
    }]
  }];
  const timeSlots = {
    day: ['00:00', '04:00', '08:00', '12:00', '16:00', '20:00'],
    week: ['', '', '', '', '', '', ''],
    month: ['1', '2', '3', '4']
  };
  const handleViewModeChange = mode => {
    setViewMode(mode);
    toast({
      title: "",
      description: `${mode === 'day' ? '' : mode === 'week' ? '' : ''}`
    });
  };
  const handleTaskClick = (task, server) => {
    toast({
      title: "",
      description: `${server} - ${task.name}`
    });
  };
  return <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-6">
      <div className="flex items-center justify-between mb-6">
        <h3 className="text-lg font-semibold text-gray-900"></h3>
        <div className="flex items-center space-x-2">
          {['day', 'week', 'month'].map(mode => <button key={mode} onClick={() => handleViewModeChange(mode)} className={`px-3 py-1 text-sm rounded transition-colors ${viewMode === mode ? 'bg-blue-600 text-white' : 'bg-gray-100 text-gray-700 hover:bg-gray-200'}`}>
              {mode === 'day' ? '' : mode === 'week' ? '' : ''}
            </button>)}
        </div>
      </div>

      <div className="overflow-x-auto">
        <div className="min-w-full">
          {/*  */}
          <div className="flex border-b border-gray-200 pb-2 mb-4">
            <div className="w-32 text-sm font-medium text-gray-700"></div>
            <div className="flex-1 flex">
              {timeSlots[viewMode].map((slot, index) => <div key={index} className="flex-1 text-center text-xs text-gray-500">
                  {slot}
                </div>)}
            </div>
          </div>

          {/*  */}
          <div className="space-y-3">
            {tasks.map((serverData, serverIndex) => <div key={serverIndex} className="flex items-center">
                <div className="w-32 text-sm text-gray-700">{serverData.server}</div>
                <div className="flex-1 h-8 bg-gray-50 rounded relative overflow-hidden">
                  {serverData.tasks.map((task, taskIndex) => <div key={taskIndex} className={`absolute h-6 ${task.color} rounded gantt-bar cursor-pointer`} style={{
                left: `${task.start}%`,
                width: `${task.duration}%`,
                top: '4px'
              }} onClick={() => handleTaskClick(task, serverData.server)} title={`${task.name} (${task.start}% - ${task.start + task.duration}%)`}>
                      <span className="text-xs text-white px-1 truncate">
                        {task.name}
                      </span>
                    </div>)}
                </div>
              </div>)}
          </div>
        </div>
      </div>

      {/*  */}
      <div className="mt-6 pt-4 border-t border-gray-200">
        <div className="flex items-center space-x-6 text-xs">
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-blue-500 rounded"></div>
            <span className="text-gray-600">BLAST</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-green-500 rounded"></div>
            <span className="text-gray-600"></span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-purple-500 rounded"></div>
            <span className="text-gray-600"></span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-amber-500 rounded"></div>
            <span className="text-gray-600"></span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-red-500 rounded"></div>
            <span className="text-gray-600"></span>
          </div>
        </div>
      </div>
    </div>;
}