console.log('home.tsx is built and running')
import React, { useState, useEffect } from 'react';
// @ts-ignore;
import { useToast } from '@/components/ui';

import Header from '@/components/Header';
import Sidebar from '@/components/Sidebar';
import DatabaseGrid from '@/components/DatabaseGrid';
import FunctionModules from '@/components/FunctionModules';
import GanttChart from '@/components/GanttChart';
import ServerStatusGrid from '@/components/ServerStatusGrid';
export default function Home(props) {
  const {
    toast
  } = useToast();
  const {
    $w,
    className,
    style
  } = props;
  const [user, setUser] = useState($w?.auth?.currentUser || {
    name: '',
    role: '',
    avatar: 'https://images.unsplash.com/photo-1472099645785-5658abf4ff4e?w=48&h=48&fit=crop&crop=face'
  });
  const [balance, setBalance] = useState(1234.56);
  const [notifications, setNotifications] = useState(5);
  const [messages, setMessages] = useState(12);
  const [reminders, setReminders] = useState(3);

  // 
  useEffect(() => {
    const interval = setInterval(() => {
      // 
      setBalance(prev => prev + (Math.random() - 0.5) * 10);
      setNotifications(prev => Math.max(0, prev + Math.floor(Math.random() * 3) - 1));
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  // API
  const fetchBioDatabases = async () => {
    try {
      // API
      const response = await fetch('/api/bio-databases');
      if (!response.ok) {
        throw new Error('');
      }
      const data = await response.json();
      return data.data || [];
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return [];
    }
  };
  const fetchServers = async () => {
    try {
      const response = await fetch('/api/servers');
      if (!response.ok) {
        throw new Error('');
      }
      const data = await response.json();
      return data.data || [];
    } catch (error) {
      console.error(':', error);
      toast({
        title: "",
        description: "",
        variant: "destructive"
      });
      return [];
    }
  };
  return <div className="min-h-screen bg-gray-50" style={style}>
      {/*  */}
      <Header user={user} notifications={notifications} onUserChange={setUser} />

      <div className="flex h-screen pt-16">
        {/*  */}
        <Sidebar user={user} balance={balance} notifications={notifications} messages={messages} reminders={reminders} />

        {/*  */}
        <main className="flex-1 overflow-y-auto bg-gray-50">
          <div className="p-6">
            {/*  */}
            <DatabaseGrid />

            {/*  */}
            <FunctionModules />

            {/*  */}
            <GanttChart />

            {/*  */}
            <ServerStatusGrid />
          </div>
        </main>
      </div>
    </div>;
}